body {
  font-size: 20px;
}
#title {
  background-color: #246fb2;
  color: white;
  padding: 20px;
  border-radius: 20px;
}

#main {
  background-color: #246fb2;
  color: white;
  padding: 20px;
  border-radius: 20px;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#submit {
  background-color: #246fb2;
  color: white;
  padding: 20px;
  border-radius: 20px;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#distance {
  background-color: white;
  width: 100px;
  height: 20px;
  margin-top: 20px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

#button {
  background-color: #ec9d23;
  border-radius: 20px;
  padding: 5px 15px 5px 15px;
  cursor: pointer;
}

#button:hover {
  background-color: black;
  transition-duration: 2s;
}

footer{
    display:none;
}
